.container {
    width: 100%;
    margin-right: auto;
    margin-left: auto;

    @media screen and (min-width: 320px) {
        width: 320px;
    }

    @media screen and (min-width: 768px) {
        width: 768px;
        padding-left: 15px;
        padding-right: 15px;
    }

    @media screen and (min-width: 1024px) {
        width: 1024px;
    }
}